// src/components/logout-button.js

import React from 'react';
import { useAuth0} from "@auth0/auth0-react";
import './CSS/index_home.css'

const LogoutButton = () => {
  const { logout } = useAuth0();
  return (
    <a
      className='topnav'
      onClick={() =>
        logout({
          returnTo: window.location.origin,
        })
      }
    >
      Log Out
    </a>
  );
};

export default LogoutButton;